export function equipmentsReducer(state = initialState, action) {
   switch (action.type) {

      case CHANGE_PLAN:
         return produce( state, setState => {
            setState
               .find( eq => eq.id === action.payload ).plan
               .map( p => p.checked = !p.checked )
         } )

      default:
         return state
   }
}